/* General App Styling */

/* Category Button Styling */

.categories button {
    background-color: unset;
    border: unset;
    padding: 5px 10px;
}

.categories button:hover {
    background-color: #563D7C; /* Previous: #692e4e */
    color: white;
}

.categories button:focus {
    outline: 0;
}

#categoryNav {
    background: unset;
    background-color: rgb(252, 250, 132);
    border-radius: unset;
    box-shadow: unset;
    justify-content: space-around;
    margin: 0 auto;
    margin-top: -20px;
    min-height: 1px;
    padding: 0;
}
/* Developer Card Styling */

.devCard {
    background: #fff;
    border-radius: 2px;
    height: 235px;
    margin: 1rem;
    /* margin-right: 0px; */
    position: relative;
    width: 180px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    padding: 0px;
    float: left;
  }

.devCard > .img-container {
    height: 50%;
    /* overflow: hidden; */
    text-align: center;
    /* background: purple; */
    background: linear-gradient(to top, #e6a8f8, #c173d0);
    
  }

.devCard > .img-container > img {
    width: 40%;
     }

#devCardImg {
    box-shadow: 2px 2px black;
    width: 118px;
    height: 118px;
    border: rgb(225, 206, 206);
    border: 2px solid white;
    border-width: 7px;
    border-radius: 50%;
    margin-top: 10px;
  }
  
.devCard > .devContent {
    padding-left: 0rem;
    padding-right: 0rem;
    font-size: 12px;
    text-align: center;
  }
  
.devCard > .devContent > ul {
    list-style-type: none;
    padding: 0px;
  }
  
.devCard > .devContent > ul > li {
    margin-top: 0px;
    font-family: Roboto, sans-serif;
  }

.devContent {
    margin-top: 15px;
}

.devTextSocial {
    text-align: center;
     color: white;
}

.fa-linkedin {
    margin-top: 10px;
    margin-bottom: 0px;
    padding: 5px;
    margin-left: 10px;
  }

.fa-github {
  padding: 5px;
  }

/* media queries */
/* small screens */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
  /* Styles */
    .navBarBtn {
        margin-top: 10px;
    }
    #logoText {
        margin-top: 12px;
        padding-left: 10px;
    }
    .signUpForm {
        margin-top: 40px;
    }
    .navBarHeader {
        max-width: 80px;
    }
    .navBarLogo {
        max-width: 80px;
    }
    div {
        width: 100%;
    }
    body {
        max-width: 400px;
    }
    .devCard {
      width: 100%;
    }
    .devText {
      width: 280px;
    }
}
/* iPad screens */
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) {
  #logoText {
      padding-left: 10px;
      padding-top: 5px;
  }
  .navBarLogo {
      left: -2px;
  }
}



  
/* NavBar Styling */

.navbar {
    box-shadow: 0 2px 4px 0 rgba(38, 47, 42, 1.0), 0 6px 15px 0 rgba(0, 0, 0, 0.19);
    border: 0px solid black;
    /* background: b-wekit-linear-gradient(bottom right, #692e4e), rgba(255, 255, 255, 1); */
    /* background: -o-linear-gradient(bottom right, #31708e, #8Fc1e3); */
    /* background: -moz-linear-gradient(bottom right, #31708e, #8Fc1e3); */
    background: linear-gradient(180deg, lightgray, white 21%, lightgray 69%);
}

#navBarLogoText {
    height: 80px;     
    padding: 5px 5px 5px 10px;
}

.navBarAppHeader {
    position: absolute;
    right: 20px;
}

#navBarAppText {
    color: white;
    font-family: 'Roboto', sans-serif;
    text-shadow: 1px 1px #262f2a;
}

/* NavBar Button\Link Styling */

.navBarBtn {
    background-color: #00467f;
    border: none;
    border-radius: 4px;
    box-shadow: 3px 3px #262f2a;
    color: white;
    font-family: inherit;
    font-size: 11px;
    font-weight: bold;
    outline: none;
    padding: 7px 0px;
    text-transform: uppercase;
    width: 100px;
}

.navBarBtn:hover {
    background-color: #00547f;
}

.navBarBtn:focus {
    outline: none;
}

.navBarBtnList {
    margin: 0;
    padding: 0;
}

.navBarBtnList li {
    display: inline;
}

.navBarBtnWrapper {
    position: absolute;
    right: 10px;
}

.navBarBtnWrapper a {
    padding: 0 10px;
}
/* User Post Styling */

.postCategoryDelBtn {
    color: rgb(252, 250, 132);
    font-size: 15px;
    font-weight: bold;
    margin: 10px 50px 0 0;
