/* Sign-Up Form Styling */

#location-input {
    height: 30px;
    padding: 4px 12px;
}

.signUpBtn {
    background-color: rgb(252, 250, 132);
    border: none;
    border-radius: 4px;
    box-shadow: 3px 3px #262f2a;
    color: black;
    font-family: inherit;
    font-size: 11px;
    font-weight: bold;
