    margin: 0;
    padding: 10px 0 27px;
    text-align: center;
    text-shadow: 1px 1px #262f2a;
}

@-webkit-keyframes shine {

    from {
        -webkit-mask-position: 150%;
    }

    to {
        -webkit-mask-position: -50%;
    }

}        

